import { Form } from 'react-bootstrap';
import validate from "./Functions/validation";
import Pure from './Pure';


class Username extends Pure {
    value = "";
    state = { error: "", validated: 0 } // 0=not validated yet, 1=not valid, 2=valid
    isValid() {
        let test = validate.safeUsername(this.value);
        this.updateState({ validated: test.valid ? 2 : 1, error: test.result });
        return test.valid;
    }
    render() {
        return <>
            <Form.Control autoFocus="on" tabIndex={this.props.tabIndex || 1} name="name" type="text" size="sm" onBlur={() => this.isValid()} onChange={e => (this.value = e.target.value)} placeholder="اسم المستخدم" />
            <div className="error ta-right">{this.state.error}</div>
        </>;
    }
}

