import { Form, Button, Col } from "react-bootstrap";
import { Link } from "react-router-dom";
import client from './CLASSES/client';
import since from "./Functions/since";
import { SnackBar, openSnackBar } from './Interfaces/SnackBar';
import { Username, Email, Password } from "./Feilds";
import Pure from './Pure';
import sharedMemory from './CLASSES/SharedMemory';
import "./Register.css";

class Register extends Pure {
    username = "";
    email = "";

    haveRegistered = false;
    state = { update: 0 }

    register(username = "", email = "") {
        this.haveRegistered = true;
        this.username = username;
        this.email = email;
        this.setState({ update: Date.now() });
    }
    render() {
        if (!client.ready)
            return <></>;
        if (this.haveRegistered)
            return <RegisterationDone username={this.username} email={this.email} />;
        else
            return <RegisterationForm onRegister={(u, e) => this.register(u, e)} />;
    }
}

class RegisterationDone extends Pure {
    render() {
        return <div className="game-register">
            <h1>التسجيل</h1>
            <div className="game-register-top">
                <div><b>تم التسجيل</b></div>
                <div>مرحباً <b>{this.props.username}</b> تم التسجيل في السيرفر</div>
                <div>يرجى الاحتفاظ في بريدكم الإلكتروني <b dir="ltr">{this.props.email}</b> في حال شراء الذهب</div>
                <div>حتى يمكنكم إستخدام نفس الذهب المشترى في سيرفرات أخرى</div>
                <br />
                <div>يمكنكم الآن <Link to="/Login">تسجيل الدخول</Link> والبدأ في اللعب</div>
                <div>نتمنى لكم أوقات ممتعة.</div>
            </div>
        </div>;
    }
}

class RegisterationForm extends Pure {
    state = { error: false, ready: false, update: 0 }
    error = { tribe: "", server: "" }
    data = {
        username: "",
        email: "",
        password: "",
        tribe: 0,
        serverNumber: 0
    }

    handleOnSubmit() {

        let err = false;
        let username = sharedMemory.get("username");
        let email = sharedMemory.get("email");
        let password = sharedMemory.get("password");
        if (!username.isValid()) err = true;
        if (!email.isValid()) err = true;
        if (!password.isValid()) err = true;

        if (this.data.tribe < 1 || this.data.tribe > 5) {
            this.error.tribe = "(الرجاء أختيار القبيلة)";
            err = true;
        }
        if (this.data.serverNumber < 1 || this.data.serverNumber > 4) {
            this.error.server = "(الرجاء أختيار السيرفر)";
            err = true;
        }

        if (err) {
            this.setState({ error: true, update: Date.now() });
            return false;
        }
        this.data.username = username.value;
        this.data.email = email.value;
        this.data.password = password.value;
        client.register(this.data).then(response => {
            if (response.done)
                this.props.onRegister(this.data.username, this.data.email);
            else if (response.unkownError)
                openSnackBar("توجد مشكلة في التسجيل الرجاء المحاولة مره أخرى.")
            else if (response.notReady)
                openSnackBar("السيرفر لم يبدأ بعد.");
            else if (response.isEnded)
                openSnackBar("لا يمكن التسجيل في سيرفر منتهي.");
            else if (response.isFull)
                openSnackBar("لا يمكن التسجيل في سيرفر ممتلئ.");
            else if (response.username_used)
                openSnackBar("أسم المستخدم مسجل من قبل.");
            else if (response.email_used)
                openSnackBar("البريد الألكتروني مستخدم من قبل.");
            else
                openSnackBar(response.e_username + response.e_password + response.e_email);
        });
        return false;
    }
    handleOnSelect(serverNumber = 1) {
        this.data.serverNumber = serverNumber;
    }

    tribeInput(value = "", name = "") {
        return <label><input type="radio" name="tribe" value={value} onChange={e => (this.data.tribe = Number(e.target.value))} /> <span>{name}</span></label>;
    }

    render() {
        return <div className="game-register">
            <SnackBar register="snackbar" />
            <h1>التسجيل</h1>
            <div className="game-register-top">
                <div><span className="green">التسجيل</span></div>
                <p>قبل التسجيل يرجى قراءة <Link to="/Rules">التعليمات</Link> لمعرفة مزايا ومساوئ القبائل</p>
            </div>
            <form onSubmit={e => { e.preventDefault(); this.handleOnSubmit(); }}>
                <Form.Group className="game-register-form">
                    <Form.Row>
                        <Form.Label column sm={2}>اسم المستخدم</Form.Label>
                        <Col>
                            <Username register="username" />
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Form.Label column sm={2}>البريد الالكتروني</Form.Label>
                        <Col>
                            <Email register="email" />
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Form.Label column sm={2}>كلمة المرور</Form.Label>
                        <Col>
                            <Password register="password" />
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Col><hr /></Col>
                    </Form.Row>
                    <Form.Row className="tribe">
                        <Col style={{ textAlign: "right" }}>
                            <div className="green">إختر القبيلة<div className="error">{this.error.tribe}</div></div>
                            <br />
                            <div style={{ textAlign: "center" }}>
                                {this.tribeInput(1, "الرومان")}
                                {this.tribeInput(2, "الجرمان")}
                                {this.tribeInput(3, "الاغريق")}
                                {this.tribeInput(4, "العرب")}
                                {this.tribeInput(5, "الفراعنة")}
                            </div>
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Col><hr /></Col>
                    </Form.Row>
                    <Form.Row className="tribe">
                        <Col style={{ textAlign: "right" }}>
                            <div className="green">إختر السيرفر<div className="error">{this.error.server}</div></div>
                            <br />
                            <div style={{ textAlign: "center", overflowX: "visible" }}>
                                <ServersList onSelect={s => this.handleOnSelect(s)} />
                            </div>
                        </Col>
                    </Form.Row>
                    <Form.Row>
                        <Col><hr /></Col>
                    </Form.Row>
                </Form.Group>
                <Button variant="secondary" type="submit">تسجيل</Button>
            </form>
        </div>;
    }
}

class ServersList extends Pure {
    state = { list: <></> }
    onReady() {
        let servers = server => <ServerCard key={server.serverNumber + "_" + server.started} data={server} onSelect={s => this.props.onSelect(s)} />;
        client.listServers(response => {
            this.updateState({ list: response.servers.map(servers) })
        });
    }
    render() {
        return this.state.list;
    }
}

/** @param {{data: { serverNumber: number, ready: boolean, ended: boolean, isFull: boolean, started: number, players: number }}} props */
function ServerCard(props) {
    let data = props.data;
    return <label className="server-card">
        <input type="radio" name="server" value={data.serverNumber} onChange={e => props.onSelect(Number(e.target.value))} />
        <div className="card-holder">
            <div className="server-card-header"><input type="checkbox" checked readOnly />السيرفر <b>{data.serverNumber}</b></div>
            <div className="server-card-content">
                <div>عدد اللاعبين: <b>{data.players}</b></div>
                <div>بدأ منذ <b>{since(data.started)}</b></div>
                <div>
                    {
                        data.ended ? <b className="error">منتهي</b> :
                            !data.ready ? <b className="error">لم يبدأ</b> :
                                data.isFull ? <b className="error">ممتلئ</b> :
                                    data.ready ? <b style={{ color: "green" }}>جاهز للعب</b> :
                                        <b style={{ color: "red" }}>غير جاهز</b>
                    }
                </div>
            </div>
        </div>
    </label>
}

export default Register;